import React, { PureComponent } from 'react'

import { FILTERS } from '../constants'
import { FilterBlock } from './FilterBlock'
import './style.css'


export class CarFilter extends PureComponent {

    render () {
        return (
            <div className="f-filterCar">
                {FILTERS.map(({ label, name }) => (
                    <FilterBlock key={name} label={label} name={name} />
                ))}
            </div>
        )
    }
}



