import { createStore as createReduxStore, combineReducers, compose, applyMiddleware } from 'redux'
import thunk from 'redux-thunk'


// REDUCERS

import { STORE_KEY as FILTER_STORE_KEY, reducer as filterReducer, middleware as filterMiddleware } from './CarFilter/store'
import { STORE_KEY as PART_TYPES_STORE_KEY, reducer as partTypesReducer } from './PartGroups/store'
import { STORE_KEY as RESULT_STORE_KEY, reducer as resultReducer, middleware as resultMiddleware } from './Result/store'
// import { reducer as form } from 'redux-form'
// import { SECURITY_SORE_KEY, securityReducer, securityMiddleware } from './base/security/store'
// import { OFFICE_STORE_KEY, officeReducer, officeMiddleware } from './page/OfficePage/store'
// import { RECORDS_STORE_KEY, recordsReducer, recordsMiddleware } from './page/OfficePage/storeRecords'
// import { STORE_KEY as REFBOOK_SORE_KEY, reducer as refbookReducer } from './base/hoc/refbook'

const finalReducer = combineReducers({
    // modal,
    // form,

    [FILTER_STORE_KEY]: filterReducer,
    [PART_TYPES_STORE_KEY]: partTypesReducer,
    [RESULT_STORE_KEY]: resultReducer,
    // [OFFICE_STORE_KEY]: officeReducer,
    // [RECORDS_STORE_KEY]: recordsReducer,
    // [REFBOOK_SORE_KEY]: refbookReducer,
})


// MIDDLEWARES


// import { createLogger } from 'redux-logger'

const finalMiddleware = compose(
    applyMiddleware(
        thunk,
        filterMiddleware,
        resultMiddleware,
        // officeMiddleware,
        // recordsMiddleware,
        // (DEVELOPMENT ? createLogger() : (store => next => action => next(action)))
    ),
    window && window.devToolsExtension ? window.devToolsExtension() : f => f
);


// STORE

export function createStore() {
    const store = createReduxStore(
        finalReducer,
        {},
        finalMiddleware
    )

    return store
}



