import React, { PureComponent } from 'react'

import './style.css'


export class Finder extends PureComponent {

    render () {
        return (
            <div className="f-finder clearfix">
                <div className="f-finder-inner">
                    <h3>Поиск по номеру запчасти</h3>
                    <input type="text"/>
                    <button>Искать</button>
                </div>
            </div>
        )
    }

}


