import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import { isFilterLoading, getFilterItems, getFilterValue, filterMounted, setValue } from './store'
import './style.css'


class FilterBlockCmp extends Component {

    static propTypes = {
        loading: PropTypes.bool.isRequired,
        label: PropTypes.string.isRequired,
        name: PropTypes.string.isRequired,
        items: PropTypes.arrayOf(PropTypes.string.isRequired),
        value: PropTypes.string,
    }

    state = {
        needScroll: false,
    }

    componentDidMount(){
        const { name, filterMounted } = this.props

        this.detectScrollbar()
        filterMounted(name)
    }

    shouldComponentUpdate (nextProps, nextState) {
        return Boolean(
            this.props.loading !== nextProps.loading ||
            this.props.items !== nextProps.items ||
            this.props.value !== nextProps.value ||
            this.state.needScroll !== nextState.needScroll
        )
    }

    componentDidUpdate(prevProps, prevState){
        this.detectScrollbar()
    }

    onSelect = (e) => {
        const { setValue, name } = this.props

        setValue(name, e.target.dataset.value)
    }

    detectScrollbar () {
        const list = this.refs.list
        const needScroll = list.offsetHeight !== list.scrollHeight
        if (needScroll !== this.state.needScroll) {
            this.setState({ needScroll })
        }
    }

    render () {
        const { loading, label, items, value } = this.props
        const { needScroll } = this.state

        return (
            <div className="f-filterCar-block">
                <label>{label}</label>
                <div className="f-filterCar-block-list-wrapper">
                    <ul ref="list" className={`f-filterCar-block-list ${needScroll ? 'need-scroll' : ''}`}>
                        {loading || !items ? null : items.map(item => (
                            <li
                                key={item}
                                className={`f-filterCar-block-item ${item === value ? 'selected' : ''}`}
                                data-value={item}
                                onClick={this.onSelect}
                            >
                                {item || 'Все'}
                            </li>
                        ))}
                    </ul>
                </div>
            </div>
        )
    }
}

export const FilterBlock = connect((state, { name }) => {
    return {
        loading: isFilterLoading(state, name),
        items: getFilterItems(state, name),
        value: getFilterValue(state, name),
    }
}, {
    filterMounted,
    setValue,
})(FilterBlockCmp)


