import axios from 'axios'


// Constatns

const PART_GROUPS = 'PART_GROUPS:'
const PART_GROUPS_MOUNTED = PART_GROUPS + 'MOUNTED'
const PART_GROUPS_LOAD_SUCCESS = PART_GROUPS + 'LOAD:SUCCESS'

const PART_GROUP = 'PART_GROUP:'
const PART_GROUP_DROP_DOWN = PART_GROUP + 'DROP_DOWN'
const PART_GROUP_DROP_UP = PART_GROUP + 'DROP_UP'

const PART_TYPES = 'PART_TYPES:'
const PART_TYPES_LOAD = PART_TYPES + 'LOAD:'
const PART_TYPES_LOAD_START = PART_TYPES_LOAD + 'START'
const PART_TYPES_LOAD_SUCCESS = PART_TYPES_LOAD + 'SUCCESS'
const PART_TYPES_LOAD_FAIL = PART_TYPES_LOAD + 'FAIL'

const PART_TYPE = 'PART_TYPE:'
export const PART_TYPE_SELECT = PART_TYPE + 'SELECT'

export const STORE_KEY = 'partGroups'

// AC

export const partGroupDropUp = (name) => ({
    type: PART_GROUP_DROP_UP,
    name,
})

export const selectPartType = (partGroup = null, partSubGroup = null) => ({
    type: PART_TYPE_SELECT,
    payload: { partGroup, partSubGroup },
})

// AC smart

export const partGroupsMounted = () => (dispatch, getState) => {
    dispatch({ type: PART_GROUPS_MOUNTED })

    axios(`/api/filter/partGroup`).then(
        ({ data }) => {
            dispatch({ type: PART_GROUPS_LOAD_SUCCESS, payload: data.filter })
            dispatch(selectPartType())
        },
        e => { console.error(e) }
    )
}

export const partGroupDropDown = name => (dispatch, getState) => {
    dispatch({ type: PART_GROUP_DROP_DOWN, name })

    const { loading, list } = getPartGroupState(getState(), name)
    if (!loading && !list) {
        dispatch({ type: PART_TYPES_LOAD_START, name })
        axios(`/api/filter/partSubGroup`, {
            params: { partGroup: name },
        }).then(
            ({ data }) => {
                const list = data.filter
                dispatch({ type: PART_TYPES_LOAD_SUCCESS, name, payload: list.length ? list : null })
            },
            e => {
                console.error(e)
                dispatch({ type: PART_TYPES_LOAD_FAIL, name })
            }
        )
    }
}

// Reducer

export function reducer(state = { groupList: [], selected: { } }, action) {
    const { name, payload } = action

    switch (action.type) {

        case PART_GROUPS_LOAD_SUCCESS:
            const initList = payload.map(name => ({ name, value: name }))
            initList.unshift({ name: 'Все записи', value: null, list: null })
            return {...state, groupList: initList }

        case PART_GROUP_DROP_DOWN:
            return modify(state, name, state => {
                if (!state.droped && state.list !== null) {
                    state = {...state, droped: true}
                }
                return state
            })

        case PART_GROUP_DROP_UP:
            return modify(state, name, state => {
                if (state.droped) {
                    state = {...state, droped: false}
                }
                return state
            })

        case PART_TYPES_LOAD_START:
            return modify(state, name, state => {
                if (!state.loading) {
                    state = {...state, loading: true}
                }
                return state
            })

        case PART_TYPES_LOAD_SUCCESS:
            return modify(state, name, state => {
                return { ...state, loading: false, list: payload }
            })

        case PART_TYPES_LOAD_FAIL:
            return modify(state, name, () => null).filter(t => t)

        case PART_TYPE_SELECT:
            return { ...state, selected: payload }

        default:
            return state
    }
}

function modify(state, name, cb) {
    const list = state.groupList
    const i = list.findIndex(item => item.name === name)

    if (i === -1) {
        return state
    }

    const newPartTypeState = cb(list[i])

    if (list[i] !== newPartTypeState) {
        const listFinal = [...list]
        listFinal[i] = newPartTypeState
        state = { ...state, groupList: listFinal }
    }

    return state
}

// Middleware



// Getters

function getPartGroups(state) {
    return state[STORE_KEY].groupList
}

export function getPartTypeSelected(state) {
    return state[STORE_KEY].selected
}

export function getPartGroupNames(state) {
    const partGroups = getPartGroups(state)
    return partGroups.map(({ name }) => name)
}

export function getPartGroupState(state, name) {
    return getPartGroups(state).filter(item => item.name === name)[0]
}


