import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import { getResultList } from './store'
import './style.css'


class ResultBase extends PureComponent {

    static propTypes = {
        loading: PropTypes.bool,
        list: PropTypes.array.isRequired,
        error: PropTypes.any,
    }

    render () {
        const { loading, list, error } = this.props

        if (error) {
            return <div>{error}</div>
        } else if (loading) {
            return <div>Loading...</div>
        }


        return (
            <div>
                {list.map(item => <div key={JSON.stringify(item)}>{JSON.stringify(item)}</div>)}
            </div>
        )
    }
}

export const Result = connect(state => {
    const { loading, list, error } = getResultList(state)
    return {
        loading,
        list,
        error,
    }
})(ResultBase)



