import axios, { CancelToken } from 'axios'

import { FILTER_NAMES } from '../constants'
import { FILTER_SELECT, FILTER_CLEAN, getFilterValue} from '../CarFilter/store'
import { PART_TYPE_SELECT, getPartTypeSelected } from '../PartGroups/store'


// Constatns

const RESULT = 'RESULT:'
const RESULT_LOAD = RESULT + 'LOAD:'
const RESULT_LOAD_START = RESULT_LOAD + 'START'
const RESULT_LOAD_SUCCESS = RESULT_LOAD + 'SUCCESS'
const RESULT_LOAD_ERROR = RESULT_LOAD + 'ERROR'
const RESULT_LOAD_CANCELLED = RESULT_LOAD + 'CANCELLED'
const RESULT_CLEAR = RESULT + 'CLEAR'


export const STORE_KEY = 'result'

// AC
// AC smart
// Reducer

export const reducer = (state = { list: [] }, action) => {
    const { type, payload } = action

    switch (type) {

        case RESULT_LOAD_START:
            return { ...state, loading: true, error: null }

        case RESULT_LOAD_SUCCESS:
            return { ...state, loading: false, list: payload }

        case RESULT_LOAD_ERROR:
            return { ...state, loading: false, list: [], error: payload }

        case RESULT_LOAD_CANCELLED:
            return { ...state, loading: false }

        case RESULT_CLEAR:
            return { ...state, list: [] }

        default:
            return state

    }
}

// Middleware

let cancel
export const middleware = store => next => action => {
    const { type } = action

    next(action)

    switch (type) {

        case FILTER_SELECT:
        case FILTER_CLEAN:
        case PART_TYPE_SELECT: {
            const state = store.getState()
            const filters = {
                ...FILTER_NAMES.reduce((out, key) => {
                    out[key] = getFilterValue(state, key)
                    return out
                }, {}),
                ...getPartTypeSelected(state),
            }

            if (cancel) {
                cancel('Input value changed')
            }

            if (!filters[FILTER_NAMES[FILTER_NAMES.length - 1]]) {
                store.dispatch({ type: RESULT_CLEAR })
                break
            }

            const params = Object.keys(filters).reduce((out, key) => {
                if (filters[key]) {
                    out[key] = filters[key]
                }
                return out
            }, {})

            const req = axios(`/api/result`, {
                params,
                cancelToken: new CancelToken(c => { cancel = c }),
            })

            store.dispatch({ type: RESULT_LOAD_START })

            req.then(
                ({ data }) => {
                    cancel = null
                    store.dispatch({type: RESULT_LOAD_SUCCESS, payload: data.list})
                },
                e => {
                    cancel = null
                    if (axios.isCancel(e)) {
                        store.dispatch({ type: RESULT_LOAD_CANCELLED, payload: (e.toString && e.toString()) || e })
                    } else {
                        store.dispatch({ type: RESULT_LOAD_ERROR, payload: (e.toString && e.toString()) || e })
                    }
                }
            )

            break
        }

        default:
            break

    }
}

// Getters

function _getResultState(state) {
    return state[STORE_KEY] || {}
}

export function getResultList(state) {
    const { loading, list, error } = _getResultState(state)
    return { loading, list, error }
}


