import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import { getPartGroupNames, partGroupsMounted } from './store'
import { PartGroup } from './PartGroup'
import './style.css'


class PartGroupsBase extends PureComponent {

    static propTypes = {
        partGroupNames: PropTypes.arrayOf(PropTypes.string).isRequired,
    };

    componentDidMount(){
        this.props.partGroupsMounted()
    }

    render () {
        const { partGroupNames } = this.props

        if (!partGroupNames) {
            return null
        }

        return (
            <div className="part-group-lits">
                {partGroupNames.map(partGroupName => <PartGroup key={partGroupName} name={partGroupName}/>)}
            </div>
        )
    }

}

export const PartGroups = connect(state => ({
    partGroupNames: getPartGroupNames(state),
}), {
    partGroupsMounted
})(PartGroupsBase)


