import React, { Component } from 'react'
import PropTypes from 'prop-types'
import axios from 'axios'


export class Autharization extends Component {

    static propTypes = {
        cb: PropTypes.func.isRequired,
    };

    state = {
        username: '',
        password: '',
    }

    changeHandler = e => {
        const { target } = e

        this.setState({ [target.name]: target.value })
    }

    submitHandler = () => {
        axios.post('api/login', this.state)
            .then(this.props.cb)
            .catch(e => console.error(e))
    }

    render () {
        const { login, pass } = this.state

        return (
            <table>
                <tbody>
                <tr>
                    <td>Логин</td>
                    <td><input type="text" name="username" value={login} onChange={this.changeHandler}/></td>
                </tr>
                <tr>
                    <td>Пароль</td>
                    <td><input type="password" name="password" value={pass} onChange={this.changeHandler}/></td>
                </tr>
                <tr>
                    <td></td>
                    <td><button onClick={this.submitHandler}>Войти</button></td>
                </tr>
                </tbody>
            </table>
        )
    }

}


