import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import { getPartGroupState, getPartTypeSelected, partGroupDropDown, partGroupDropUp, selectPartType } from './store'
import './style.css'


export class PartGroupBase extends PureComponent {

    static propTypes = {
        name: PropTypes.string.isRequired,
        value: PropTypes.string,
        droped: PropTypes.bool,
        list: PropTypes.any,
        selected: PropTypes.shape({
            partGroup: PropTypes.string,
            partSubGroup: PropTypes.string,
        }).isRequired,

        partGroupDropUp: PropTypes.func.isRequired,
        partGroupDropDown: PropTypes.func.isRequired,
        selectPartType: PropTypes.func.isRequired,
    };

    headerClick = () => {
        const { value, list, selectPartType } = this.props

        if (list === null) {
            selectPartType(value)
        } else {
            this.partGroupToggleDrop()
        }
    }

    partGroupToggleDrop = () => {
        const { name, droped, partGroupDropUp, partGroupDropDown } = this.props

        if (droped) {
            partGroupDropUp(name)
        } else {
            partGroupDropDown(name)
        }
    }

    onSelect = e => {
        const { value, selectPartType } = this.props

        selectPartType(value, e.currentTarget.dataset.value)
    }

    render () {
        const { name, droped, list, value, selected } = this.props

        return (
            <div className="part-group">
                <div onClick={this.headerClick} className="part-group-header">
                    {name}
                    {list === null && <div className={`header-selectable ${selected.partGroup === value ? 'selected' : ''}`} /> }
                    {list !== null && <div className={`header-dropable ${droped ? 'droped' : ''}`} /> }
                </div>
                { droped && list === undefined && <div>Loading...</div> }
                { droped && list &&
                    <ul className="part-group-list">
                        {list.map(typeName => (
                            <li
                                key={typeName}
                                className={`part-group-list-item ${selected.partGroup === value && selected.partSubGroup === typeName ? 'selected' : ''}`}
                                data-value={typeName}
                                onClick={this.onSelect}
                            >
                                {typeName}
                            </li>
                        ))}
                    </ul>
                }
            </div>
        )
    }

}

export const PartGroup = connect(
    (state, { name }) => {
        const { value, droped, list } = getPartGroupState(state, name)
        return {
            value,
            droped,
            list,
            selected: getPartTypeSelected(state)
        }
    },
    {
        partGroupDropDown,
        partGroupDropUp,
        selectPartType,
    }
)(PartGroupBase)


