import axios, { CancelToken } from 'axios'
import { combineReducers } from 'redux'

import { FILTER_NAMES } from '../constants'


// Constatns

const CAR_FILTER = 'CAR_FILTER:'
const FILTER_BLOCK_MOUNTED = CAR_FILTER + 'MOUNTED'
const FILTER_LOAD = CAR_FILTER + 'LOAD:'
const FILTER_LOAD_START = FILTER_LOAD + 'START'
const FILTER_LOAD_SUCCESS = FILTER_LOAD + 'SUCCESS'
const FILTER_LOAD_ERROR = FILTER_LOAD + 'ERROR'
const FILTER_LOAD_CANCEL = FILTER_LOAD + 'CANCEL'
export const FILTER_SELECT = CAR_FILTER + 'SELECT'
export const FILTER_CLEAN = CAR_FILTER + 'CLEAN'


export const STORE_KEY = 'carfilter'

// AC

export const filterMounted = (name) => ({
    type: FILTER_BLOCK_MOUNTED,
    name,
})

export const filterLoadStart = (name, { req, cancel }) => ({
    type: FILTER_LOAD_START,
    name,
    payload: { req, cancel }
})

export const filterLoadSuccess = (name, items) => ({
    type: FILTER_LOAD_SUCCESS,
    name,
    payload: items
})

export const filterLoadError = (name) => ({
    type: FILTER_LOAD_ERROR,
    name,
})

export const filterLoadCancel = (name) => ({
    type: FILTER_LOAD_CANCEL,
    name,
})

export const setValue = (name, value) => ({
    type: FILTER_SELECT,
    name,
    payload: value
})

export const clean = (name) => ({
    type: FILTER_CLEAN,
    name,
})

// AC smart

export const loadFilterItems = name => (dispatch, getState) => {
    let { req, cancel } = getFilterState(getState(), name)

    if (req) {
        cancel('Operation canceled by another request.')
        dispatch(filterLoadCancel(name))
    }

    const params = {}
    const state = getState()
    for (let i = FILTER_NAMES.indexOf(name) - 1; i >= 0; i--) {
        const name = FILTER_NAMES[i]
        const value = getFilterValue(state, name)
        if (value) {
            params[name] = value
        }
    }

    req = axios(`/api/filter/${name}`, {
        params,
        cancelToken: new CancelToken(c => { cancel = c }),
    })

    dispatch(filterLoadStart(name, { req, cancel }))

    req.then(
        ({ data }) => dispatch(filterLoadSuccess(name, data.filter)),
        e => {
            if (!axios.isCancel(e)) {
                dispatch(filterLoadError(name))
            }
        }
    )
}

// Reducer

export const reducer = combineReducers(
    FILTER_NAMES.reduce(
        (out, name) => {
            out[name] = filterReducerFabrica(name)
            return out
        },
        {}
    )
)

function filterReducerFabrica(name) {
    return (state, action) => {
        const { payload } = action
        state = state || {}

        if (action.name === name) {
            switch (action.type) {

                case FILTER_LOAD_START:
                    return { ...state, req: payload.req, cancel: payload.cancel, items: null, value: null }

                case FILTER_LOAD_SUCCESS:
                    return { ...state, req: null, cancel: null, items: [...payload] }

                case FILTER_LOAD_ERROR:
                case FILTER_LOAD_CANCEL:
                case FILTER_CLEAN:
                    return { ...state, req: null, cancel: null, items: null, value: null }

                case FILTER_SELECT:
                    return { ...state, value: payload }

                default:
                    return state
            }
        }

        return state
    }
}

// Middleware

export const middleware = store => next => action => {
    const { name } = action

    if (action.type === FILTER_SELECT && getFilterValue(store.getState(), name) === action.payload) {
        return
    }

    next(action)

    switch (action.type) {

        case FILTER_BLOCK_MOUNTED:
            if (name === FILTER_NAMES[0]) {
                store.dispatch(loadFilterItems(name))
            }
            break;

        case FILTER_SELECT: {
            const changedFilterPos = FILTER_NAMES.indexOf(action.name)
            const state = store.getState()
            for (let i = changedFilterPos + 1; i < FILTER_NAMES.length; i++) {
                const name = FILTER_NAMES[i]
                if (getFilterItems(state, name) || getFilterValue(state, name)) {
                    store.dispatch(clean(name))
                }
            }

            const lastFilter = changedFilterPos === FILTER_NAMES.length - 1

            if (lastFilter) {
                // Дальше грузить нечего
            } else {
                store.dispatch(loadFilterItems(FILTER_NAMES[changedFilterPos + 1]))
            }

            break
        }

        default:
            break

    }
}

// Getters

function getFilterState(state = {}, key) {
    return state[STORE_KEY][key] || {}
}

export function getFilterItems(state = {}, key) {
    const filterState = getFilterState(state, key)
    return filterState.items
}

export function getFilterValue(state = {}, key) {
    const filterState = getFilterState(state, key)
    return filterState.value || (filterState.value === '' ? '' : null)
}

export function isFilterLoading(state = {}, key) {
    const filterState = getFilterState(state, key)
    return Boolean(filterState.req)
}


