import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import { FILTER_NAMES } from '../constants'
import { getFilterValue } from '../CarFilter/store'


function FilterSummaryBase (props) {
    const { values } = props
    return (
        <div className="f-filterCar-summary">Автомобиль: {values}</div>
    )
}

FilterSummaryBase.propTypes = {
    values: PropTypes.string.isRequired,
}

export const FilterSummary = connect(state => {
    const values = FILTER_NAMES
        .map(key => getFilterValue(state, key))
        .filter(t => t)
        .join(', ')

    return { values }
})(FilterSummaryBase)


