import React, { Component } from 'react'
import axios from 'axios'

import { Autharization } from './Autharization'
import { FileUpload } from './FileUpload'


export class Upload extends Component {

    state = {
        loading: true,
    }

    componentDidMount(){
        this.tryAuth()
    }

    tryAuth = () => {
        this.setState({ loading: true })
        axios.post('api/user')
            .then(({ data: { authorized }}) => {
                this.setState({ loading: false, authorized })
            })
            .catch(e => console.error(e))
    }

    logoutHandler = e => {
        e.preventDefault()
        axios.post('api/logout')
            .then(this.tryAuth)
            .catch(e => console.error(e))
    }

    render () {
        const { loading, authorized } = this.state

        if (loading) {
            return (
                <div>
                    Loading...
                </div>
            )
        }

        if (authorized) {
            return (
                <div>
                    <div><a onClick={this.logoutHandler} href="api/logout">Выход</a></div>
                    <FileUpload/>
                </div>
            )
        }

        return (
            <Autharization cb={this.tryAuth}/>
        )
    }

}


